import React from 'react';
import { FormField, Input } from '@wix/design-system';
import { BaseInputProps } from './types';
import {
  useEntityPageContext,
  useSelector,
  useWixPatternsContainer,
} from '@wix/patterns';
import { useController } from '@wix/patterns/form';

export interface ShortTextProps extends BaseInputProps<string> {}

export const ShortText = (props: ShortTextProps) => {
  const { field, dataHook } = props;

  const { translate: t, ...container } = useWixPatternsContainer();
  useSelector(() => container.initTask.status);

  const pageState = useEntityPageContext();

  const stringLengthRange = field.validation?.stringLengthRange;
  const minLength = stringLengthRange?.minLength;
  const maxLength = stringLengthRange?.maxLength;

  const controller = useController({
    name: field?.id ?? '',
    control: pageState.form.control,
    defaultValue: (pageState.entity as any)?.[field?.id ?? ''],
    rules: {
      validate: (value) => {
        if (
          field.validation?.required &&
          (value === null || value === undefined || value === '')
        ) {
          return t('cairo.fieldValidation.requiredField');
        }

        if (minLength && value && value.length < minLength) {
          return t('cairo.fieldValidation.minCharacter', {
            charMin: minLength,
          });
        } else if (maxLength && value && value.length > maxLength) {
          return t('cairo.fieldValidation.maxCharacter', {
            charMax: maxLength,
          });
        }

        return true;
      },
    },
  });

  const charCount = maxLength
    ? maxLength - controller.field.value?.length
    : undefined;

  return (
    <FormField
      label={field.displayName}
      required={field.validation?.required}
      charCount={charCount}
      status={controller.fieldState.error ? 'error' : undefined}
      statusMessage={controller.fieldState.error?.message}
      dataHook={dataHook}
    >
      <Input
        value={controller.field.value}
        onChange={(e) => controller.field.onChange?.(e.target.value)}
        onBlur={controller.field.onBlur}
        dataHook={`short-text-${field.id}`}
        ref={controller.field.ref}
      />
    </FormField>
  );
};
